
.green {
  color: #42b983;
}
.cover {
  padding: 100px 0;
}
.cover .cover-inner {
  margin: 0 auto;
  max-width: 800px;
}
.cover .cover-inner .cover-pic {
  float: left;
  width: 350px;
  margin-right: 0;
  border-radius: 10px;
}
.cover .cover-inner .cover-description-title {
  font-family: 'Dosis', 'Source Sans Pro', 'Helvetica Neue', Arial, sans-serif;
  font-size: 28px;
  padding-top: 20px;
  padding-bottom: 15px;
}
.cover .cover-inner .cover-description-content {
  font-size: 18px;
  line-height: 1.5;
}
.cover .cover-inner .light {
  font-size: 16px;
}


