
.top-nav-container {
  position: fixed;
  width: 100%;
  top: 0;
  background: #fff;
  z-index: 1;
  box-shadow: 0 0 4px rgba(0,0,0,0.25);
}
.top-nav {
  height: 40px;
  padding: 15px 100px 15px 60px;
}
.top-nav .search-container {
  float: right;
  position: relative;
}
.top-nav .search-container .search {
  height: 30px;
  line-height: 30px;
  box-sizing: border-box;
  padding: 0 15px 0 30px;
  border: 1px solid #e3e3e3;
  color: #2c3e50;
  outline: none;
  border-radius: 15px;
  margin-right: 10px;
  margin-top: 5px;
  transition: border-color 0.2s ease;
  background: #fff url("../../assets/search.png") 8px 5px no-repeat;
  background-size: 20px;
}
.top-nav .search-container .search.active {
  border-color: #42b983;
}
.top-nav .search-container .result-list {
  position: absolute;
  top: 30px;
  left: 0;
  list-style-type: none;
  background-color: #fff;
  border: 1px solid #bbb;
  border-radius: 4px;
  font-size: 16px;
  margin: 10px 0 0;
  padding: 8px;
  text-align: left;
}
.top-nav .search-container .result-list a {
  color: #34495e;
}
.top-nav .search-container .result-list a:hover {
  color: #42b983;
}
@media screen and (min-width: 720px) {
.top-nav .search-container .result-list {
    width: 300px;
}
}
.nav-logo {
  display: inline-block;
}
.nav-logo .logo {
  margin-right: 6px;
}
.logo {
  width: 40px;
  height: 40px;
}
.brand {
  font-size: 1.5em;
  color: #2c3e50;
  font-family: 'Dosis', 'Source Sans Pro', 'Helvetica Neue', Arial, sans-serif;
  font-weight: 500;
}
/**
  fixme! 留作以后的slogan样式
*/
/*.slogan
  font-size 1.3em
  color $light
  font-family $logo-font
  font-weight 500*/
.nav-link-container {
  float: right;
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.nav-link-item {
  display: inline-block;
  margin: 0 0.6em;
}
.nav-link {
  height: 40px;
  line-height: 40px;
  text-decoration: none;
  color: #34495e;
  padding-bottom: 3px;
}
.nav-link:hover,
.nav-link.router-link-active {
  border-bottom: 3px solid #42b983;
}
.github-logo {
  width: 30px;
  vertical-align: -3px;
}


