
.sidebar {
  position: fixed;
  left: 0;
  top: 70px;
  bottom: 0;
  padding: 40px 0 50px 65px;
  width: 260px;
  margin-right: 20px;
  overflow-x: hidden;
  overflow-y: auto;
}
.sidebar .question-links-container {
  padding-left: 0;
  margin: 0;
}
.sidebar .question-links-container .question-link {
  list-style-type: none;
  line-height: 1.8em;
}
.sidebar .question-links-container .question-link a {
  color: #7f8c8d;
}
.sidebar .question-links-container .question-link a:hover,
.sidebar .question-links-container .question-link a.router-link-active {
  color: #42b983;
}


